.timeline-container{
	position: relative;
	max-width: 1200px;
	margin: 0 auto;	
}
.timeline-container::after {
    content: '';
    position: absolute;
    width: 2px;
    background-color: rgba(204, 204, 204);
    top: 0;
    bottom: 0;
    margin-left: -5px;
}
.timeline {
	position: relative;
    margin-left: 21px;
    margin-bottom: 5%;
}
.timeline::before {
	content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: var(--surface);
    left: -34px;
    top: 4px;
    margin-left: 0%;
    z-index: 1;
	box-shadow: #ccc 0px 0px 12px 2px;
}